@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;700;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Lato", sans-serif;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.Login {
  height: 100vh;
  background: url(/grana-admin/static/media/login-bg.af9df279.png) no-repeat center center fixed;
  background-size: cover;
}

.Login .login-container {
  height: 100%;
  background: rgba(35, 67, 85, 0.67);
  display: flex;
  align-items: center;
  justify-content: center;
}

.Login form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Login form input {
  background: transparent;
  border: 2px solid white;
  width: 100%;
  padding: 1rem;
  margin-bottom: 1rem;
  border-radius: 10px;
  color: white;
}

.Login form button {
  background: #f57547;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 5px;
  color: white;
  border: none;
  width: 100%;
  height: 60px;
  margin-top: 1rem;
  font-weight: 600;
  font-size: 24px;
  line-height: 29px;
}

.Login p {
  font-size: 32px;
  line-height: 38px;
  margin-top: 0.3rem;
  color: white;
  margin-bottom: 2.5rem;
}

.Login ::-webkit-input-placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: white;
  opacity: 1; /* Firefox */
}

.Login :-ms-input-placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: white;
  opacity: 1; /* Firefox */
}

.Login ::placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: white;
  opacity: 1; /* Firefox */
}

.Login:-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: white;
}

.Login::-ms-input-placeholder {
  /* Microsoft Edge */
  color: white;
}

.Login .input-feedback {
  display: block;
  color: red;
  font-weight: bold;
  margin-top: 1rem;
}

.Login .none {
  display: none;
}

.Dreamers {
  display: flex;
  background: #f2f5f7;
  height: 100vh;
}

.Dreamers .dreamers-container {
  width: 80%;
  margin: 0 auto;
  margin-top: 2rem;
}

.Dreamers .dreamers-content {
  margin-top: 1rem;
}

.Sidebar {
  background: #234355;
  height: 100vh;
  padding: 3rem 0 3rem 0.2rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: fixed;
}

.Sidebar .sidebar-top {
  display: flex;
  flex-direction: column;
  width: 80px;
}

.Sidebar a {
  margin-bottom: 2.5rem;
  padding: 1rem 0.7rem 1rem 0.2rem;
  border-radius: 4rem 0 0 4rem;
}
.Sidebar .sidebar-top a:hover,
.active {
  background: rgba(79, 105, 119, 1);
  width: 100%;
}

.Sidebar .export {
  margin-bottom: 2.5rem;
  padding: 1rem 0.7rem 1rem 0.2rem;
  border-radius: 4rem 0 0 4rem;
  object-fit: none;
  cursor: pointer;

}
.Sidebar .export:hover{
	background: rgba(79, 105, 119, 1);
}

.Sidebar .sidebar-bottom span {
  font-style: normal;
  font-weight: 300;
  color: white;
  font-size: 16px;
  line-height: 24px;
  padding: 0 0.5rem 0 0;
  cursor: pointer;
}

.Modal .modal-container {
  display: flex;
  flex-direction: column;
  padding: 0.3rem 1.5rem 1.5rem 1.5rem;
  margin: 0;
}

.Modal h2 {
  font-family: Lato;
  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  line-height: 20px;
  letter-spacing: 0.25px;
  text-transform: uppercase;
  color: #000000;
}

.Modal p {
  font-family: Lato;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 0.25px;
  color: #000000;
  margin-top: 1.4rem;
  margin-bottom: 0.7rem;
}

.Modal .buttons-modal {
  display: flex;
  justify-content: space-between;
}

.Modal .buttons button {
  background: #db511f;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 5px;
  font-family: Lato;
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  color: #ffffff;
  padding: 0.5rem;
  width: 50%;
  border: none;
}

.Modal .buttons-modal button:first-child {
  background: #ffffff;
  border: 2px solid #db511f;
  box-sizing: border-box;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  margin-right: 0.4rem;
  text-transform: uppercase;
  color: #db511f;
}

.Modal .single {
  width: 100%;
  display: flex;
  justify-content: center;
}

.Modal .single button {
  background: #db511f;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 5px;
  font-family: Lato;
  font-style: normal;
  font-weight: 600;
  font-size: 24px;
  line-height: 29px;
  border: none;
  color: #ffffff;
  width: 80%;
  padding: 0.5rem;
}

.Modal .close {
  margin-right: 0.7rem;
  padding-top: 0.6rem;
}

.Modal .download-sheets-button-area button {
  background: #db511f;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 5px;
  font-family: Lato;
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  color: #ffffff;
  padding: 0.5rem;
  width: 100%;
  border: none;
  margin-top: 10px;
  margin-bottom: 10px;
}

.Header {
  display: flex;
  justify-content: space-between;
  padding-bottom: 0.6rem;
  border-bottom: 2px solid #234355;
}

.Header .header-left {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  text-align: left;
}

.Header .header-left span {
  font-size: 20px;
  line-height: 20px;
  letter-spacing: 0.25px;
  color: #234355;
}

.Header .header-left span:first-of-type {
  font-weight: bold;
  margin-bottom: 0.3rem;
}

.Header .header-right {
  display: flex;
}

.Header .header-right span:first-of-type {
  font-weight: bold;
  font-size: 36px;
  line-height: 36px;
  letter-spacing: 0.25px;
  text-transform: uppercase;
  color: #234355;
  margin-right: 1rem;
}

.Header .header-right span {
  font-size: 20px;
  line-height: 27px;
  letter-spacing: 0.25px;
  text-transform: uppercase;
  color: #234355;
}

.tbody:nth-child(odd) {
  background: white;
  border: 2px solid rgba(71, 137, 173, 0.1);
}

.tbody:nth-child(even) {
  background: rgba(174, 195, 209, 0.2);
  border: 2px solid rgba(71, 137, 173, 0.1);
}

.tbody:nth-child(odd):hover {
  background: rgba(35, 67, 85, 0.58);
  cursor: pointer;
}

.tbody:nth-child(even):hover {
  background: rgba(35, 67, 85, 0.58);
  cursor: pointer;
}

td {
  padding: 0.8rem;
  font-size: 20px;
  line-height: 24px;
  text-align: center;
}

td:first-child {
  padding-left: 2rem;
  font-weight: bold;
  font-size: 20px;
  line-height: 20px;
  letter-spacing: 0.25px;
  text-transform: uppercase;
  color: #000000;
  text-align: left;
  border-radius: 10px 0 0 10px;
}

td:last-child {
  border-radius: 0 10px 10px 0;
}

.placeholder {
  margin-top: 2rem;
  margin-bottom: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.placeholder span {
  font-size: 24px;
  line-height: 24px;
  letter-spacing: 0.25px;
  color: #234355;
  margin-right: 1rem;
  cursor: pointer;
}

.placeholder img {
  height: 20px;
}

.Pagination .current {
  padding: 0.3rem;
  background: #4789ad;
}

.table {
  width: 100%;
  text-align: left;
  border-spacing: 0 1rem;
}

.table th:first-of-type {
  padding-left: 2rem;
  text-align: left;
}

.table th {
  font-size: 16px;
  line-height: 20px;
  letter-spacing: 0.25px;
  text-transform: uppercase;
  color: #234355;
  padding: 0 0 1.5rem;
}

.table thead th {
  text-align: center;
}

.SingleDreamer {
  display: flex;
  background: #f2f5f7;
  flex-direction: column;
}

.SingleDreamer .dreamers-container {
  width: 80%;
  margin: 0 auto;
  margin-top: 2rem;
}

.SingleDreamer .dreamers-content {
  width: 60%;
  margin: 0 auto;
  margin-top: 3rem;
}

.SingleDreamer .header {
  display: flex;
  align-items: center;
  border-bottom: 2px solid #234355;
  padding-bottom: 1rem;
}

.SingleDreamer .header img {
  margin-right: 1rem;
  border-radius: 50%;
  width: 4.5em;
  height: 4.5em;
  object-fit: cover;
}

.SingleDreamer .header h2 {
  font-size: 48px;
  text-align: center;
  letter-spacing: 0.25px;
}

.SingleDreamer .content {
  margin-top: 1.5rem;
  display: flex;
  justify-content: space-between;
}

.SingleDreamer .content .left,
.SingleDreamer .content .right {
  width: 48%;
  text-align: left;
}

.SingleDreamer .content span {
  font-weight: bold;
  font-size: 20px;
  text-transform: uppercase;
  margin-bottom: 1rem;
}

.SingleDreamer .content .left {
  display: flex;
  flex-direction: column;
}

.SingleDreamer .content textarea {
  border: 1px solid #234355;
  border-radius: 10px;
  height: 540px;
  padding: 1em;
  line-height: 1.5em;
}
.SingleDreamer .right .progress {
  margin-bottom: 2rem;
}

.SingleDreamer .dreamer-quotas {
  width: 30%;
  display: flex;
  margin-left: 2rem;
  flex-direction: column;
}

.SingleDreamer .right .progress .quota:first-child {
  border-radius: 5px 0px 0px 5px;
}

.SingleDreamer .right .progress .quota:last-child {
  border-radius: 0px 5px 5px 0px;
}

.SingleDreamer .content .numbers {
  display: flex;
  margin-top: 1rem;
  margin-bottom: 3rem;
}

.SingleDreamer .content .numbers .goal {
  width: 50%;
  display: flex;
  flex-direction: column;
}

.SingleDreamer .content .numbers input {
  border: 1px solid #234355;
  border-radius: 20px;
  width: 176px;
  height: 38px;
  margin-top: 1rem;
  padding: 1em;
}

.SingleDreamer .content .risk {
  margin-top: 2rem;
  margin-bottom: 1rem;
}

.SingleDreamer .content .buttons {
  margin-top: 1rem;
  display: flex;
  justify-content: space-between;
}

.SingleDreamer .content .risk .buttons button {
  background: #7e7e7e;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border: none;
  color: white;
  padding: 1rem;
  font-size: 1.2rem;
  margin-right: 0.5rem;
  margin-left: 0.5rem;
  width: 100%;
}

.SingleDreamer .content .risk .buttons button:first-child {
  border-radius: 10px 0px 0px 10px;
  margin-right: 0;
  margin-left: 0;
}

.SingleDreamer .content .risk .buttons button:last-child {
  border-radius: 0 10px 10px 0;
  margin-right: 0;
  margin-left: 0;
}

.SingleDreamer .supporters {
  margin-top: 2rem;
  margin-bottom: 2rem;
}

.SingleDreamer .supporters span {
  display: flex;
  justify-content: space-between;
}

.SingleDreamer .supporters span span {
  font-size: 0.8rem;
}

.SingleDreamer .supporters p {
  margin-top: 1rem;
}

.SingleDreamer .status .buttons button {
  border-radius: 5px;
  background: #7e7e7e;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border: none;
  color: white;
  padding: 1.5rem;
  font-size: 1.2rem;
  width: 100%;
}

.SingleDreamer .status .buttons button:first-child {
  margin-right: 1rem;
}

.SingleDreamer .dreamer-profile {
  margin: 0 auto;
  margin-top: 5em;
}

.SingleDreamer .dreamer-profile-content {
  display: flex;
  justify-content: space-between;
}

.SingleDreamer .dreamer-profile-content .left,
.SingleDreamer .dreamer-profile-content .right {
  width: 48%;
  text-align: left;
}

.SingleDreamer .dreamer-profile h3 {
  text-align: left;
  font-weight: bold;
  font-size: 36px;
  line-height: 27px;

  letter-spacing: 0.25px;
  margin-bottom: 2rem;
}

.SingleDreamer .dreamer-profile .profile-header {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid black;
  padding-bottom: 1rem;
  margin-bottom: 1.5rem;
}

.SingleDreamer .dreamer-profile .profile-header h4 {
  font-weight: bold;
  font-size: 24px;
  line-height: 27px;
  letter-spacing: 0.25px;
  text-transform: uppercase;
}

.SingleDreamer .dreamer-profile .profile-header span {
  font-weight: 300;
  font-size: 18px;
  line-height: 24px;
  letter-spacing: 0.25px;
  color: #000000;
}

.SingleDreamer .dreamer-profile .profile-header span span {
  font-weight: bold;
  font-size: 20px;
  line-height: 20px;
  letter-spacing: 0.25px;
  text-transform: uppercase;
  color: #000000;
}

.SingleDreamer .dreamer-profile .question {
  margin-bottom: 2rem;
}

.SingleDreamer .dreamer-profile .question {
  font-size: 16px;
  line-height: 24px;
}

.SingleDreamer .dreamer-profile .question span {
  font-weight: bold;
  font-size: 18px;
  line-height: 24px;
}

.SingleDreamer .profile label {
  font-weight: bold;
  font-size: 16px;
  line-height: 20px;
  letter-spacing: 0.25px;
}

.SingleDreamer .profile label input {
  border: 1px solid #234355;
  width: 100%;
  padding: 0.8rem;
  border-radius: 20px;
  margin-top: 1rem;
  margin-bottom: 1.5rem;
}

.SingleDreamer .profile label input[type="file"] {
  border: 0;
  clip: rect(0, 0, 0, 0);
  height: 1px;
  overflow: hidden;
  padding: 0;
  position: absolute !important;
  white-space: nowrap;
  width: 1px;
}

.SingleDreamer .profile .file {
  margin-top: 1rem;
  border: 2px solid #db511f;
  box-sizing: border-box;
  box-shadow: 0px 4px 4px rgb(0 0 0 / 25%);
  border-radius: 30px;
  width: 100%;
  display: block;
  padding: 0.8rem;
  color: #db511f;
  text-align: center;
}

.SingleDreamer .footer {
  background: #2a6687;
  height: 90px;
  margin-top: 4rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.SingleDreamer .footer button {
  width: 384px;
  height: 55px;
  margin-right: 2rem;
  background: #db511f;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 5px;
  font-weight: 600;
  font-size: 24px;
  line-height: 29px;

  color: #ffffff;
}

.SingleDreamer .footer button:first-child {
  background: #ffffff;
  border: 2px solid #db511f;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 5px;
  color: #db511f;
  font-weight: 600;
  font-size: 18px;
  text-transform: uppercase;
}

.SingleDreamer .active-btn {
  background: #00ba77 !important;
}

.SingleDreamer .orange {
  background: #f57547 !important;
}

