.placeholder {
  margin-top: 2rem;
  margin-bottom: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.placeholder span {
  font-size: 24px;
  line-height: 24px;
  letter-spacing: 0.25px;
  color: #234355;
  margin-right: 1rem;
  cursor: pointer;
}

.placeholder img {
  height: 20px;
}

.Pagination .current {
  padding: 0.3rem;
  background: #4789ad;
}
